* {
    margin: 0;
    padding: 0;
}

.App::before {
    content: "";
    background: url("./img/pic2.jpg") no-repeat center center/cover;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.inp-field {
    text-align: center;
    padding: 1rem;
}

.inp-field input {
    padding: 0.7rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 15px;
    border: 1px solid grey;
    background: rgba(255, 255, 255, 0.5);
    color: #8f8f8f;
}
.btn_d {
    display: flex;
    justify-content: center;
}

.btn {
    appearance: none;
    background: rgba(255, 255, 255, 0.5);
    border: 1px solid grey;
    border-radius: 20px;
    box-sizing: border-box;
    color: #3b3b3b;
    cursor: pointer;
    display: inline-block;
    font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI",
        Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
        "Segoe UI Symbol";
    font-size: 1rem;
    font-weight: 600;
    line-height: normal;
    margin: 0;
    min-height: 45px;
    min-width: 0;
    outline: none;
    padding: 8px 12px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    will-change: transform;
}

.btn:disabled {
    pointer-events: none;
}

.btn:hover {
    color: #fff;
    background-color: #1a1a1a;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
}

.btn:active {
    box-shadow: none;
    transform: translateY(0);
}

.container {
    text-align: center;
    margin-top: 4vh;
    font-style: oblique;
    font-size: 3rem;
    color: #fff;
}

.container h1 {
    font-size: 4rem;
}
.container h2 {
    font-size: 2rem;
}
